.accountdatacontainer {
    background-color: #435c70;
    padding: 40px;
  }
  .accountdatacontainer .inputcontainer {
    display: flex;
  }
  .accountdatacontainer .inputcontainer .inputfields {
    width: 100%;
    margin-right: 40px;
  }
  .accountdatacontainer .inputcontainer:first-child .inputfields {
    margin-right: 80px;
  }
  .container {
    display: flex;
  }
  .accountdatacontainer:first-child {
    margin-right: 30px;
    width: 30%;
  }
  .accountdatacontainer img {
    width: 100%;
    height: auto;
  }
  /* input {
    width: 100%;
  } */
  .accountdatacontainer:last-child {
    width: 70%;
  }
  .btn {
    width: 115%;
  }
  button {
    margin-top: 28px;
  }
  .imgcontainer {
    position: relative;
  }
  .imgcontainer .imgoverlay {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .imgcontainer .imgoverlay i {
    color: #fff;
    background: rgba(57, 78, 100, 0.7);
    padding: 15px;
    font-size: 20px;
    border-radius: 100%;
  }
  .imgcontainer:hover .imgoverlay {
    display: initial;
    cursor: pointer;
  }
  
  @media (max-width: 767px) {
    .container {
      display: block;
    }
    .accountdatacontainer:first-child {
      margin: 0;
      width: 81%;
      margin-bottom: 30px;
    }
    .accountdatacontainer:last-child {
      width: 81%;
    }
    .accountdatacontainer .inputcontainer {
      display: block;
    }
    .btn {
      width: 100%;
    }
  }
  
  .modal {
    width: 40%;
    height: 20%;
    background-color: #435c70;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    position: relative;
  }
  .modal h1 {
    text-transform: capitalize;
    font-weight: 500;
    letter-spacing: 0.5px;
  }
  .modalouter {
    position: absolute;
    top: 0px;
    background: #252525b3;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 150vh;
  }
  .modal i {
    position: absolute;
    right: 15px;
    top: 15px;
  }